import React, {useEffect, useState} from 'react';
import {NavLink, Routes, Route} from "react-router-dom";
import {motion} from "framer-motion";


import RegisterStep1 from "./registerStep1/RegisterStep1";
import RegisterStep2 from "./registerStep2/RegisterStep2";

import "./register.sass"
import {containerStudentsNum, itemStudentsNum} from "../../frame-motion";




const Register = () => {
    const [userData,setUserData] = useState(null)

    const updateUserData = (data) => {
        console.log(data)
        setUserData((prevUser) => ({ ...prevUser, ...data }));
    }

    useEffect(()=>{
        console.log(userData)
    },[userData])

    const handleClick = (e) => {
        e.preventDefault()
    }
    
    



    return (
        <motion.div className="register">


            <motion.div
                variants={containerStudentsNum}
                initial="hidden"
                exit="exit"
                animate="show"
                className="steps"
            >
                <motion.div className="steps__item" variants={itemStudentsNum}>
                    <NavLink
                        to="step_1"
                        className={
                            ({ isActive }) =>
                                "steps__item-link" + (isActive ? " steps__item-link_active" : "")
                        }
                        onClick={handleClick}
                    >
                        <span className="circle">
                            1
                        </span>
                        <span>
                            Step
                        </span>
                    </NavLink>
                </motion.div>
                <motion.div className="steps__item" variants={itemStudentsNum}>
                    <NavLink
                        to="step_2"
                        className={
                            ({ isActive }) =>
                                "steps__item-link" + (isActive ? " steps__item-link_active" : "")
                        }
                        onClick={handleClick}

                    >
                        <span className="circle">
                            2
                        </span>
                        <span>
                            Step
                        </span>
                    </NavLink>
                </motion.div>
            </motion.div>

            <Routes>
                <Route path="step_1" element={<RegisterStep1 userData={userData} updateUserData={updateUserData}/>}/>
                <Route path="step_2" element={<RegisterStep2 userData={userData} updateUserData={updateUserData}/>}/>
            </Routes>

        </motion.div>


    );
};

export default Register;